}

.container{
max-width: 1440px;
}

.section{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding-top: 40px;
}

.section-one{
  display: flex;
  margin-top: 50px;
  flex-direction: column;
}

.section-one{
  text-align: center;
}

.section-one input{
  max-width: 30%;
